.social__list {
  list-style: none;

  outline: 3px solid rgba(255, 0, 0, 0.8);
  outline-offset: -3px;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 0;
  padding: 0;
}

.social__link {
  font-size: 0;
}

.social {
  width: 130px;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    margin: auto;
  }
}
